/**
 * imui.Datepicker - DateInput
 * @author riverhan
 * @data 2016-8-10
 */

import React, { PropTypes } from 'react';
import classnames from 'classnames';

import { isDayInMonth, isSameDate, compareDate, isDateInRange } from './dateUtils';

class Day extends React.Component {
  static propTypes = {
    day: PropTypes.object.isRequired,
    monthDay: PropTypes.object.isRequired,
    onDayClick: PropTypes.func.isRequired
  };

  static defaultProps = {
    day: new Date(),
    monthDay: new Date()
  };

  constructor(props) {
    super(props);

    this.handleClick = this.handleClick.bind(this);
    const { day, minDate, maxDate } = this.props;

    this.state = {
      disabled: (minDate && compareDate(day, minDate) < 0) ||
        (maxDate && compareDate(day, maxDate) > 0)
    };
  }

  handleClick() {
    if (!this.state.disabled) {
      this.props.onDayClick(this.props.day);
    }
  }

  render() {
    const { day, monthDay, weekMode, selectedRange } = this.props;
    const showDay = isDayInMonth(day, monthDay);
    let isSelected = false;

    if (weekMode) {
      if (selectedRange && isDateInRange(day, selectedRange.from, selectedRange.to)) {
        isSelected = true;
      }
    } else {
      if (isSameDate(day, this.props.selected)) {
        isSelected = true;
      }
    }

    return (
      <span
        className={classnames({
          'im-dp-day': true,
          'im-dp-day--unvisual': !showDay && !weekMode,
          selected: isSelected,
          disabled: this.state.disabled
        })}
        onClick={this.handleClick}
      >
        {day.getDate()}
      </span>
    );
  }
}

export default Day;
